import React, { useEffect } from "react";
import Home from "./Home";
import {
  createBrowserRouter,
  createRoutesFromElements,
  Outlet,
  Route,
  RouterProvider,
} from "react-router-dom";
import Navbarr from "./Navbarr";
import "bootstrap/dist/css/bootstrap.min.css";
import "swiper/css";
import "swiper/css/effect-fade";
import "swiper/css/navigation";
import 'swiper/css/free-mode';
import 'swiper/css/thumbs';

import "swiper/css/pagination";
import "./App.css";
import ScrollToTop from "./ScrollToTop";
import About from "./About";
import Contact from "./Contact";
import AddNew from "./AddNew";
import Signup from "./Signup";
import SignIn from "./SignIn";
// import NotFound from "./NotFound";
import ForgetPass from "./ForgetPass";
import EditProfile from "./EditProfile";
import ContactDone from "./ContactDone";
import Favorite from "./Favorite";
import { GoogleOAuthProvider } from "@react-oauth/google";
import ResetPass from "./ResetPass";
import NotFound from "./NotFound";
import ResetetPassSu from "./ResetetPassSu";


function App() {



  const router = createBrowserRouter(
    createRoutesFromElements(
      <>
        <Route path="/" element={<Root />}>
          <Route index element={<Home />} />
          <Route path="/About" element={<About />} />
          <Route path="/Contact" element={<Contact />} />
          <Route path="/Signup" element={<Signup />} />
          <Route path="/AddNew" element={<AddNew />} />
          <Route path="/SignIn" element={<SignIn />} />
          <Route path="/ForgetPass" element={<ForgetPass />} />
          <Route path="/Profile" element={<EditProfile />} />
          <Route path="/ContactDone" element={<ContactDone />} />
          <Route path="/Favorite" element={<Favorite />} />
          <Route path="/ResetPass" element={<ResetPass />} />
          <Route path="/reset-pass-success" element={<ResetetPassSu />} /> 
          <Route path="*" element={<NotFound />} />

          
        </Route>
      </>
    )
  );

  return (
    <GoogleOAuthProvider clientId="861487734698-glnb3pt6hq5hthbialuf3tio4cfnfcsj.apps.googleusercontent.com">

    <div className={`app`}>
      <RouterProvider router={router}></RouterProvider>
    </div>
    </GoogleOAuthProvider>
  );
}

export default App;

const Root = () => {


  return (
    <div className={`App`}>
      <ScrollToTop />

      <Navbarr />
      <Outlet />
    </div>
  );
};
