import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Reviews({ restRoom }) {
    const [reviews, setReviews] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchReviewsRestrooms = async () => {
            try {
                // const token = localStorage.getItem('authToken');

                // if (!token) {
                //     setError("Authorization token is missing.");
                //     setLoading(false);
                //     return;
                // }

                const response = await axios.get(
                    `https://api.rerests.com/api/v1/review/${restRoom}`
                );

                if (response && response.data.data.reviews) {
                    const reviewsData = response.data.data.reviews;

                    const reviewsElements = reviewsData.map((res) => (
                        <div key={res.id} className="frame-parent12">
                            <div className="row7">
                                <div className="divblock1">
                                    <div className="div14">
                                        <img className="user-image" alt={res.user.name} src={res.user.image || "Rectangle.png"} />
                                        <div className="text">
                                            <div className="courtney-henry">{res.user.name}</div>
                                            <div className="row8">
                                                {[...Array(res.user.rating)].map((_, i) => (
                                                    <img key={i} className="row-child" alt="star" src="./assets/img/star.svg" />
                                                ))}
                                                
                                                <div className="mins-ago">2 mins ago</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="consequat-velit-qui">{res.comment}</div>
                            </div>
                        </div>
                    ));

                    setReviews(reviewsElements);
                }
            } catch (error) {
                console.error('Error fetching restrooms:', error);
                setError(error.response?.data?.message || 'An unexpected error occurred.');
            } finally {
                setLoading(false);
            }
        };

        fetchReviewsRestrooms();
    }, [restRoom]);  // Add restRoom as a dependency if it might change

    if (loading) {
        return <div>Loading...</div>;
    }

    if (error) {
        return <div>Loading...</div>;
    }

    return (
        <div>
            {reviews.length > 0 ? reviews : 
            <div className='reviewempty'>
<img src="./assets/img/pana.svg" alt="" />
<h3>No reviews yet</h3>
<p>Be the first person to add a review <br />for this place now!</p>

            </div>}
        </div>
    );
}

export default Reviews;
